/* number */

.number {
    background-color: #10c9c3;
}
.number__wrapper {
    padding: 100px 100px 100px 100px;
}
@media (max-width: 991px) {
    .number__wrapper{
    padding: 50px 50px;
}}
.number__item {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.number__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
} 
.number__item-text {
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    padding-left: 28px;
    line-height: 30px;
}
.number__item-text > span {
    font-size: 12px;
}
.number__image {
    padding-left: 69px;
}

@media (max-width: 991px) {
    .number__image {
        padding-left: 33px;
    }
}
