.skill {
    background-color: #f8f9f9;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.skill__wrapper {
    width: 50%;
    display: flex;
    flex-direction: row;
    padding-top: 139px;
}
.skill__value {
    padding-bottom: 165px;
    margin-right: 0;
}
.skill__value-tittle {
    color: #171717;
    font-family: 'Montserrat', sans-serif;
    font-size: 30px;
    font-weight: 500;
    line-height: 84px;
}
.skill__value-item_name {
    color: #000000;
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    font-weight: 500;
    padding-bottom: 6px;
    padding-top: 40px;
    text-transform: uppercase;
    line-height: 30px;
}
.skill__value-item_bar {
    padding-right: 85px;
    width: 500px;
    height: 3px;
    background-color: #d8d8d8;
    box-sizing: border-box;
}
.skill__bar-UI {
    background-color: #10c9c3;
    width: 75%;
    height: 100%;
}
.skill__bar-web {
    background-color: #10c9c3;
    width: 90%;
    height: 100%;
}
.skill__bar-marketing {
    background-color: #10c9c3;
    width: 60%;
    height: 100%;
}
.skill__img {
    color: #e9e9e9;
    width: 50%;

}
.skill__img > img {
    width: 100%;
    height: 100%;
}