.about {
    padding-top: 100px;
    width: 620px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}
.about__tittle {
    font-family: 'Montserrat', sans-serif;
    font-size: 30px;
    text-align: center;
    margin-bottom: 40px;
}
.about__text {
    text-align: center;
    color: #787878;
    font-family: "Nunito Sans";
    font-size: 16px;
    line-height: 24px;
}
.about__signature {
    display: flex;
    justify-content: center;
    
    padding-top: 40px;
    padding-bottom: 100px;
}
