

/* обратная свзяь*/



.form {
    background: #F5F5F5;
    display: none;
}
.form__wrapper {
    padding-left: 347px;
    padding-right: 347px;
}
.form__container {
    padding-top: 100px;
    padding-bottom: 100px;
    display: flex;
    flex-direction: column;
}

.form__head {
    width: 537px;
    margin: 0 auto;
}

.form__head--title {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 84px;
    text-align: center;
}

.form__head--text {
    text-align: center;
    font-family: 'Nunito Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.08px;
    color: #787878;
    padding-bottom: 40px;
}

.form__group {
    display: flex;
    flex-direction: row;
    padding-bottom: 27px;
    justify-content: space-between;   
}

.form__group--input {
    height: 51px;
    width: 100%;
    padding-left: 22px;
    box-sizing: border-box;
}
.form__group--email {
    width: 100%;
    margin-left: 27px;
}
.form__group--name {
    width: 100%;
}
.form__title {
    width: 100%;
    height: 51px;
    padding-bottom: 27px;
}

.form__comment {
    width: 100%;
    height: 173px;
    margin-bottom: 27px;
}


.form__title--input {
    padding-left: 22px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}
.form__comment--input {
    padding-top: 15.5px;
    padding-left: 22px;
    box-sizing: border-box;
    padding-bottom: 133px;
    width: 100%;
    height: 100%;
}

.form__btn {
    display: flex;
    justify-content: center;
}

input {
    color: #787878;
    font-family: 'Nunito Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
}
