
.nav {
    padding-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-family: 'Montserrat', sans-serif;
}

.nav-bar {
    display: flex;
    align-self: center;
}
.nav-bar__item {
    margin-left: 31px;
    font-weight: 600;
    font-size: 12px;
    line-height: 24px;
    color: black;
}

.nav-bar__item:hover, .nav__logo:hover {
    opacity: .7;
    transition: .2s;
}