/*footer*/

footer {
    display: none;
    background: #141414;
}

.footer__wrapper {
    padding-top: 100px;
    padding-bottom: 100px;
}
.footer__container {
    display: flex;
    flex-direction: row;
    height: 87px;
}
.footer__contacts {
    margin-left: 246px;
}
.footer__inc {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: 0.18px;
}
.footer__links--item {
    font-family: 'Nunito Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
    letter-spacing: 0.075px;
    color: #BCBCBC;
}
.footer__links--item:hover {
    opacity: .6;
    transition: .2s;
}
.footer__links--container {
    margin-left: 145px;
    display: flex;
    flex-direction: row;
    width: 165px;
    justify-content: space-between;
}
.footer__links {
    display: flex;
    flex-direction: column;
}
.footer__networks {
    display: flex;
    flex-direction: column;
    margin-left: 135px;
}
a {
    text-decoration: none;
}
