.main__button {
    width: 180px;
    height: 55px;
    background-color: #10c9c3;
    text-decoration: none;
    border: none;
    cursor: pointer;
    margin-left: 5px;
}
.main__button:hover {
    background-color: #787878;
    transition: .4s;
}

.main__button--text {
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;

}