/* основной блок */
.gallery__wrapper {
    width: 100%;
    position: relative;
    
}
.gallery {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.image {
    position: relative;
    width: 25%;
}
  .image__item {
    width: 100%;
    height: 100%;
}
  .image__item > img {
    width: 100%;
    height: 100%;
}
  .image__sub-item {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(0, -50%, 0);
    background: black;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    opacity: 0;
}
  .image__sub-item > img {
    margin: 0 auto;
    display: block;
}

  .image__sub-item:hover {
    opacity: .8;
    transition: .2s linear;
    cursor: pointer;

}

/*конец основного*/



/* кнопка 'load more' */

.load-more {
    height: 100px;
}
.load-more__button:hover {
    cursor: pointer;
}
.load-more__button {
    width: 100%;
    height: 100%;
    text-decoration: none;
    background: #F5F5F5;
    border: none;
}
.load-more__text {
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    color: #8E8E8E;
}
