.banner {
    width: 479px;
    margin-left: auto;
    margin-right: 25px;
    margin-top: 175px;
}
@media (max-width: 991px) {
    .banner {
        margin-top: 120px;
    }
}
@media (max-width: 768px) {
    .banner {
        margin-top: 50px;
    }
}
.banner__tittle {
    font-weight: 600;
    font-size: 38px;
    font-family: 'Montserrat', sans-serif;
    line-height: 48px;
}
.banner__text {
    font-size: 18px;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 5px;
    line-height: 28px;
    color: #787878;
    font-family: 'Nunito Sans', sans-serif;
}
