

.head-wrapper {

    background: url('img/preview.png');
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100%;
}

.wrapper {
    width: 100%;
    height: 100vh;
    margin: 0 auto;
}

.content {
    padding: 0 calc(50% - 470px);
}

@media (max-width: 768px) {
    .content {
        padding: 0 20px;
        margin: 0 auto;
    }
}